video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.hero-container {
  background: url('../../public/welcome-background.png') center center/cover no-repeat;
  height: 100vh;
  width: 100%;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
}

.content {
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.3);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  object-fit: contain;
}

.content > h1 {
  color: rgba(85, 107, 47, 1);
  font-size: 100px;
  margin-top: -100px;
  text-shadow: rgba(85, 107, 47, 0.9);
}

.content > p {
  width: 60%;
  margin-top: 8px;
  color: #6b2f02;
  font-size: 22px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
  'Lucida Sans', Arial, sans-serif;
  font-weight: bold;
  text-align: center;
}

.hero-sub {
  margin-top: 40px;
  font-size: 24px;
  color: rgb(73, 30, 3);
  font-weight: bold;
  text-align: center;
}

.hero-btns {
  margin-top: 32px;
  flex-direction: row;
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }

  .hero-container > p {
    font-size: 30px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}