.graph4 {
  background-color: white;
  margin: auto;
  padding: 5rem 1rem 5rem 2rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  object-fit: contain;
}

.graph4 h1 {
  text-align: center;
  color: rosybrown;
  font-size: 3rem;
  margin-bottom: 25px;
}

.graph4 p {
  width: 60%;
  text-align: center;
  color: rgba(85, 107, 47);
  font-size: 20px;
  margin-bottom: 1rem;
}

.graph4 .clusters {
  display: grid;
  width: 90%;
  margin: auto;
  justify-content: center;
  align-items: center;
  padding: 1rem 1rem 1rem 1rem;
  grid-template-columns: repeat(5, 1fr);
}

.graph4 h2 {
  font-size: 2rem;
  color: white;
}









